<template>
    <div id="topicDetailPage" class="p-lr-15">
        <section class="ub ub-ac p-tb-10 bb-1">
             <Avatar :src="article.Avatar" size="large" />
             <div class="ub-f1 ml-20">
                 <p class="text-16" v-text="article.Auther"></p>
                 <p v-text="formateDate(article.Created)"></p>
             </div>
        </section>
        <div class="p-tb-10">
            <h3 class="mb-10" v-text="article.Title"></h3>
            <p class="line-h-20" v-text="article.Content"></p>
            <p class="p-tb-10" v-show="article.RecommendText!=''"><Icon type="pricetag"></Icon><span class="ml-10" v-text="article.RecommendText"></span></p>
        </div>
        <div class="ub ub-ac bg-f0 br-3 p-10">
            <p class="ub-f1" v-text="'浏览量：'+article.Views"></p>
            <span v-text="'回复：'+article.ReplayCount"></span>
        </div>
    </div>
</template>

<script>
export default {
name:"topicDetailPage",
props:["article"],
created(){
    
},
methods:{
    formateDate:function(str){
        return this.base.formateDate(str);
    }
}
}
</script>

<style>

</style>



