import Vue from 'vue'
import Router from 'vue-router'
import Home from "@/components/Home/index"
import userList from "@/components/user/userList"
import topicList from "@/components/topic/topicList"
import topicDetail from "@/components/topic/topicDetail"
import topicDetailPage from "@/components/topic/topicDetailPage"
import addArticle from "@/components/topic/addArticle"
import login from "@/components/login/login"
import admin from "@/admin.vue"
import register from "@/components/login/register"
import error from "@/components/error/err"

Vue.use(Router)
export default new Router({
    routes: [
        //内容操作
        {
            path: '/',
            component: admin,
            children: [
                { path: '/', name: 'Home', component: Home },
                { path: '/userList', name: 'userList', component: userList },
                { path: '/topicList', name: 'topicList', component: topicList },
                { path: '/topicDetail/:topicId', name: 'topicDetail', component: topicDetail },
                { path: '/topicDetailPage/:topicId', name: 'topicDetailPage', component: topicDetailPage },
                { path: '/addArticle', name: 'addArticle', component: addArticle },
            ]
        },
        //登录注册
        { path: '/login', name: 'login', component: login },
        { path: '/register', name: 'register', component: register },
        { path: '/error', name: 'error', component: error },
    ]
})


