
<template>
<div id="userList">
    <searchBar :data="searchData" v-on:search="searchList" />
    
    <div class="mt-20">
        <Table :columns="tabelColumns" :data="tableData"></Table>
    </div>
    <div class="mt-20 clearfix">
        <div class="float-r">
             <Page :total="100" show-sizer show-elevator @on-change="pagechange" @on-page-size-change="pageSizeChange"></Page>
        </div>
    </div>
</div>
</template>
<script>
import changeUser from "@/components/user/changeUser"
import searchBar from "@/components/searchBar"
    export default {
        name:"userList",
        components:{changeUser,searchBar},
        data () {
            return {
                searchData:[
                    {type:"input",
                    key:"uid",
                    value:"",
                    placehoder:"用户名称"
                    },{
                        type:"select",
                        key:"userGroup",
                        value:"",
                        options:[{value:0,label:"管理员"},{value:1,label:"普通用户"}]
                    },
                    {type:"date",
                    key:"created",
                    value:"",
                    }
                ],
                cityList: [
                    {
                        value: 'New York',
                        label: 'New York'
                    }
                ],
                usergroup: '',
                created: '',
                tabelColumns: [
                    {
                        title: '用户id',
                        key: 'Uid'
                    },
                    {
                        title: '用户名',
                        key: 'Name',
                    },
                    {
                        title: '年龄',
                        key: 'Age',
                        sortable:true,
                    },
                    {
                        title: '性别',
                        key: 'Sex',
                        filters:[
                            {
                                label:"男",
                                value:0,
                            },{
                                label:"女",
                                value:1
                            }
                        ],
                        filterMultiple:false,
                        filterMethod(value,row){
                            return row.Sex==value;
                        },
                        render:(h,params)=>{
                            var s="";
                            switch(params.row["Sex"]){
                                case 0:s="男";break;
                                case 1:s="女";break
                                default:s="未知";
                            }
                            return h("span",s)
                        }
                    },
                    {
                        title:"用户组",
                        key:"Usergroup",
                        filters: [
                            {
                                label: '管理员',
                                value: 0
                            },
                            {
                                label: '普通用户',
                                value: 1
                            }
                        ],
                        filterMultiple: false,
                        filterMethod (value, row) {
                           return row.Usergroup==value;
                        },
                        render:(h,params)=>{
                            var group="";
                            switch(params.row["Usergroup"]){
                                case 0:group="管理员";break;
                                case 1:group="普通用户";break
                                default:group="普通用户";
                            }
                            return h("span",group)
                        }
                    },
                    {
                        title:"手机号码",
                        key:"Phone",
                    },
                    {
                        title: 'Action',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, 'View'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, 'Delete')
                            ]);
                        }
                    }
                ],
                tableData:[]
            }
        },methods:{
            pageSizeChange(size){
                console.log(size)
            },
            pagechange(page){
                console.log(page);
            },
            searchList(){
                //此处执行搜索ajax请求
            },
            getArticleList(){
                var t=this;
                $.ajax({
                    url:t.$root.baseUrl+"/user/GetFilterUserList",
                    type:"get",
                    success:function(d){
                        t.tableData=d;
                    }
                })
            },
            show (index) {
                this.$Modal.confirm({
                    render: (h) => {
                        return h("div",[
                            h(changeUser,{
                                props: {
                                    "userInfo":this.tableData[index],
                                    "groups":[{value:0,label:"管理员"},{value:1,label:"普通用户"}]
                                     }
                                })                            
                        ])
                    }
                })
            },
            remove (index) {
                this.tableData.splice(index, 1);
            }
        },created(){
           this.getArticleList()
            
        }
    }
</script>



