<template>
    <div id="searchBar">
        <template v-for="(item,index) in data">
            <Select v-if="item.type=='select'" v-model="data[index].value" style="width:100px;margin-right:5px;">
                <Option v-for="op in item.options" :value="op.value" :key="op.value">{{ op.label }}</Option>
            </Select>
             <Input v-if="item.type=='input'" v-model="item.value" :placeholder="item.placehoder" style="width: 100px;margin-right:5px;"></Input>
        </template>
        <Button type="primary" @click="searchList()">查询</Button></div>
    </div>
</template>

<script>
export default {
    name:"searchBar",
    props:["data"],
    methods:{
        searchList:function(){
            var searchItem={}
            this.data.map(function(item,index){
                searchItem[item.key]=item.value;
            })
            this.$emit("search",searchItem)
        }
    }
}
</script>

<style>

</style>




