<style scoped>
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
        height: 100vh;
    }
    .layout-header-bar{
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .layout-logo-left{
        width: 90%;
        height: 30px;
        background: #5b6270;
        border-radius: 3px;
        margin: 15px auto;
    }
    .ivu-menu{margin-right: -1px;}
    .menu-icon{
        transition: all .3s;
    }
    .rotate-icon{
        transform: rotate(-90deg);
    }
    .menu-item span{
        display: inline-block;
        overflow: hidden;
        width: 69px;
        text-overflow: ellipsis;
        white-space: nowrap;
        vertical-align: bottom;
        transition: width .2s ease .2s;
    }
    .menu-item i{
        transform: translateX(0px);
        transition: font-size .2s ease, transform .2s ease;
        vertical-align: middle;
        font-size: 16px;
    }
    .collapsed-menu span{
        width: 0px;
        transition: width .2s ease;
    }
    .collapsed-menu i{
        transform: translateX(5px);
        transition: font-size .2s ease .2s, transform .2s ease .2s;
        vertical-align: middle;
        font-size: 22px;
    }
    .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu){border-right: 3px solid #2d8cf0;background: #f5f5f5;}
    .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) a{color: #2d8cf0;}
    a{color: #5b6270}
    .ivu-layout-header{height: auto;line-height: auto;}
    .ivu-layout{height: 100vh;}
</style>
<style>
    .ivu-layout-sider-children{height: 100vh;background:#fff;border-right:1px solid #dddeee;overflow-x:hidden;}
    .ivu-layout-sider-collapsed .ivu-menu .ivu-menu{display: none;}
    .ivu-layout-sider-collapsed .ivu-menu .title{display: none;}
    .ivu-layout-sider-collapsed .ivu-menu i{transform: translateX(5px);transition: font-size .2s ease .2s, transform .2s ease .2s;vertical-align: middle;font-size: 22px;}
</style>

<template>
    <div class="layout">
        <Layout>
            <Sider ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed">
                <div style="width:200px">
                    <div style="padding:20px;"><img style="width:40px;height:40px;border-radius:4px;vertical-align:middle;" src="/static/images/logo.png"><b style="margin-left:20px;font-size:20px;vertical-align:middle;">牛票票</b></div>
                    <Menu active-name="1-2" theme="light" width="auto" :open-names="['1']">
                        <Submenu name="1">
                            <template slot="title">
                            <Icon type="person-stalker" @click.stop="showMenue"></Icon>
                                <span class="title">用户管理</span>
                            </template>
                            <MenuItem name="userList"><router-link to="/userList">用户列表</router-link></MenuItem>
                        </Submenu>
                        <Submenu name="2">
                            <template slot="title">
                                <Icon type="navicon-round" @click.stop="showMenue"></Icon>
                                <span class="title">内容管理</span>
                            </template>
                            <MenuItem name="AuditTopicList"><router-link to="/AuditTopicList">H帖子审核列表</router-link></MenuItem>
                            <MenuItem name="TopicList"><router-link to="/TopicList">帖子列表</router-link></MenuItem>
                            <MenuItem name="HitTopicAuditList"><router-link to="/HitTopicAuditList">中奖审核列表</router-link></MenuItem>
                            <MenuItem name="RecomandList"><router-link to="/RecomandList">首页推荐列表</router-link></MenuItem>
                            <MenuItem name="ReplayManager"><router-link to="/ReplayManager">评论管理</router-link></MenuItem>
                            <MenuItem name="addArticle"><router-link to="/addArticle">内容补发</router-link></MenuItem>
                        </Submenu>
                        <Submenu name="3">
                            <template slot="title">
                            <Icon type="ribbon-b" @click.stop="showMenue"></Icon>
                                <span class="title">全局推荐</span>
                            </template>
                            <MenuItem name="manage"><router-link to="/manage">公共管理</router-link></MenuItem>
                            <MenuItem name="recomandPic"><router-link to="/recomandPic">图文推荐</router-link></MenuItem>
                        </Submenu>
                        <Submenu name="4">
                            <template slot="title">
                            <Icon type="clipboard" @click.stop="showMenue"></Icon>
                                <span class="title">版面管理</span>
                            </template>
                            <MenuItem name="panelList"><router-link to="/panelList">版面列表</router-link></MenuItem>
                            <MenuItem name="moderatoerList"><router-link to="/moderatoerList">版主列表</router-link></MenuItem>
                        </Submenu>
                        <Submenu name="5">
                            <template slot="title">
                            <Icon type="social-android" @click.stop="showMenue"></Icon>
                                <span class="title">Android APP</span>
                            </template>
                            <MenuItem name="APK"><router-link to="/APK">APK文件</router-link></MenuItem>
                            <MenuItem name="addPackage"><router-link to="/addPackage">增量包</router-link></MenuItem>
                        </Submenu>
                        <Submenu name="6">
                            <template slot="title">
                            <Icon type="android-settings" @click.stop="showMenue"></Icon>
                                <span class="title">系统管理</span>
                            </template>
                            <MenuItem name="Account"><router-link to="/Account">后台账户</router-link></MenuItem>
                            <MenuItem name="settings"><router-link to="/settings">全局配置</router-link></MenuItem>
                            <MenuItem name="exit"><router-link to="/exit">退出系统</router-link></MenuItem>
                        </Submenu>
                    </Menu>
                </div>
            </Sider>
            <Layout>
                <Header :style="{background: '#fff',zIndex:'1', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}">
                    <div class="ub ub-ac">
                        <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '0 20px 0 -30px'}" type="navicon-round" size="24"></Icon>
                        <div class="ub-f1"><h1>{{title}}</h1></div>
                        <div><Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" /> <b>胡吃海喝</b></div>
                        <div class="ml-20"><Icon type="email" size="24" style="vertical-align:middle"></Icon> <b>我的消息</b></div>
                        <div class="ml-20"><Icon type="android-exit" size="24" style="vertical-align:middle"></Icon> <b>退出</b></div>
                    </div>
                </Header>
                <Content :style="{position:'relative',}">
                    <div style="position:absolute;width:100%;height:100%;overflow-y:scroll;">
                        <Card style="margin:20px;">
                            <router-view/> 
                        </Card>
                    </div>
                </Content>
            </Layout>
        </Layout>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                title:"欢迎来到牛票票管理后台",
                isCollapsed: false
            }
        },
        computed: {
            rotateIcon () {
                return [
                    'menu-icon',
                    this.isCollapsed ? 'rotate-icon' : ''
                ];
            },
            menuitemClasses () {
                return [
                    'menu-item',
                    this.isCollapsed ? 'collapsed-menu' : ''
                ]
            }
        },
        methods: {
            collapsedSider () {
                this.$refs.side1.toggleCollapse();
            },showMenue(){
                this.isCollapsed=false;
            }
        }
    }
</script>



