<template>
    <div id="changeUser">
        <Row class="mb-10" v-for="(item,key) in userInfo" :key="key">
            <Col span="6" class="text-right" style="line-height:32px;"><span style="margin-right:10px;">{{key}}:</span></Col>
            <Col v-if="key!='Usergroup'" span="18"><Input v-model="userInfo[key]" disabled placeholder="请输入"/></Input></Col>
            <Col v-else span="18">
                <Select v-model="userInfo.Usergroup" style="width:200px">
                    <Option v-for="item in groups" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
            </Col>
        </Row>
    </div>
</template>
<style scoped>

</style>
<script>
    export default {
        name:"changeUser",
        props:["userInfo","groups"],
        created(){
        }
    }
</script>



