<template>
    <div id="addArticle">
        <Form :label-width="80" >
            <FormItem label="补发标题">
                <Input v-model="article.Title" name="title" placeholder="请输入标题"/>
                <p v-show="articleValid.title" class="text-red">请输入标题</p>
            </FormItem>
            <FormItem label="补发内容">
                <Input v-model="article.Content" type="textarea" :autosize="{minRows: 5,maxRows: 10}" placeholder="请输入内容"/>
                 <p v-show="articleValid.content" class="text-red">请输入内容</p>
            </FormItem>
            <FormItem label="补发用户">
                 <Select v-model="article.Uid" filterable>
                    <Option v-for="item in userList" :value="item.Uid" :key="item.Uid">{{ item.Name }}</Option>
                </Select>
            </FormItem>
            <FormItem label="推荐语">
                <Input v-model="article.RecommendText"  placeholder="请输入推荐语"/>
            </FormItem>
            <FormItem label="帖子状态">
               <Select v-model="article.State">
                    <Option v-for="(value,key) in articleState" :key="key" :value="key" >{{value}}</Option>
                </Select>
            </FormItem>
            <FormItem label="补发时间">
                <Row>
                    <Col span="22">
                        <DatePicker v-model="created" type="datetime" placeholder="Select date"></DatePicker>
                    </Col>
                </Row>
            </FormItem>
             <FormItem>
                <Button type="primary" @click="addArticle()">补发</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
export default {
name:"addArticle",
data:()=>{
    return {
        title:"内容补发",
        created:null,
        articleState:{0:'普通',1:'推荐',2:'下沉',3:'被删除',4:'审核未过',},
        userList:[],
        dateTime:{
            day:"",
            time:""
        },
        user:{uid:null,uname:null},
        article:{},
        articleValid:{
            title:false,
            content:false,
        }
    }
},
methods:{
    searchUser:function(){
        var t=this;
        $.ajax({
            url:t.base.baseUrl+"/searchUserName",
            data:{name:t.article.Auther},
            type:"get",
            success:function(d){
                t.userList=d.Data;
            }
        })
    },
    addArticle:function(name){
        var validate=true;
        var t=this;
        (!t.base.thereIs(t.article.Title))?t.articleValid.title=true:t.articleValid.title=false;
       (!t.base.thereIs(t.article.Content))?t.articleValid.content=true:t.articleValid.content=false;
       
        if(t.article.Uid){
            for(var i=0;i<t.userList.length;i++){
                if(t.article.Uid==t.userList[i].Uid){
                    t.article.Auther=t.userList[i].Name;
                    break;
                }
            }
        }
        if(t.created){
            t.article.Created=Date.parse(t.created)/1000;
        }
        for(var key in t.articleValid){
            if(t.articleValid[key]==true){
                validate=false
                break;
            }
        }
        

        if (!validate){
            return false;
        }
        $.ajax({
            url:t.base.baseUrl+"/addArticle",
            data:JSON.stringify(t.article),
            dataType:"json",
            type:"post",
            processData: false, // 告诉jQuery不要去处理发送的数据
            contentType: false,
            xhrFields: {
                withCredentials: true
            },
            success:function(d){
                console.log(2);
                if(d.Code==0){
                    t.article={};
                     this.$Message.confirm('Clicked cancel');
                }
            },error:function(d){
                console.log(d)
            }
            
        })
    }
},created:function(){
    this.searchUser();
}
}
</script>

<style>

</style>



