
<template>
<div id="userList">
    <searchBar :data="searchBarData" v-on:search="getTopicList" />
    
    <div class="mt-20">
        <Table :loading="loading" :columns="tabelColumns" :data="tableData"></Table>
    </div>
    <div class="mt-20 clearfix">
        <div class="float-r">
             <Page :total="ArticleTotalCount" show-sizer show-elevator @on-change="pagechange" @on-page-size-change="pageSizeChange"></Page>
        </div>
    </div>
</div>
</template>
<script>
import changeUser from "@/components/user/changeUser"
import searchBar from "@/components/searchBar"
    export default {
        loading:false,
        name:"userList",
        components:{changeUser,searchBar},
        data () {
            return {
                ArticleTotalCount:0,
                searchBarData:[
                    {type:"input",
                    key:"auther",
                    value:"",
                    placehoder:"作者"
                    },
                    {
                        type:"select",
                        key:"state",
                        value:"",
                        options:[{value:"",label:"全部"},{value:0,label:"普通"},{value:1,label:"推荐"},{value:2,label:"下沉"},{value:3,label:"被删除"},{value:4,label:"审核未通过"},]
                    },
                ],
                tabelColumns: [
                    {title:"文章ID",key:"Aid",fixed: 'left',width:80,},
                    {title: '文章标题',key: 'Title',width:120,fixed:"left"},
                    {title: '用户id',key: 'Uid',width:100},
                    {title: '作者',key: 'Auther',width:100,},
                    {title: '文章内容',key: 'Content',width:400},
                    {title: '帖子状态',key: 'State',width:120,
                    render:(h,params)=>{
                        var state="", type="ghost";
                        switch(params.row.State){
                            // 0：普通；1：推荐；2下沉；3：被删除；4：审核未过
                            case 0:state="普通";type="ghost";break;
                            case 1:state="推荐";type="success";break;
                            case 2:state="下沉";type="warning";break;
                            case 3:state="被删除";type="info";break;
                            case 4:state="审核未通过";type="error";break;
                            default:state="普通";
                        }
                        return h("Button",
                        {props: {
                            type: type,
                            size: 'small'
                        }},state)
                    }
                    },
                    {title:"推荐语",key:"RecommendText",width:150},
                    {title: '发帖时间',key: 'Created',width:120},
                    {title: '浏览量',key: 'Views',width:100},
                    {title: '最新更新时间',key: 'Update',width:120},
                    {title: '最新回复时间',key: 'ReplayTime',width:120},
                    {title: '总回复数',key: 'ReplayCount',width:100},
                    {title: '最新回复用户ID',key: 'ReplayLastUserId',width:100},
                   {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        fixed:"right",
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                             this.$router.push({path: 'topicDetail/'+params.row.Aid});
                                        }
                                    }
                                }, '查看详情'),
                            ]);
                        }
                    }
                ],
                tableData:[],
                searchOptions:{
                    page:1,
                    limit:10,
                    userName:"",
                }
            }
        },methods:{
            pageSizeChange(limit){
                var t=this;
                t.searchOptions.limit=limit;
                t.getTopicList()
            },
            pagechange(page){
                var t=this;
                t.searchOptions.page=page;
                t.getTopicList()
            },
            getTopicList(search){
                var t=this;
                $.extend(t.searchOptions,search)
                $.ajax({
                url:t.base.baseUrl+"/getArticleList",
                type:"get",
                data:t.searchOptions,
                beforeSend:function(){t.loading=true},
                success:function(d){
                    
                    t.ArticleTotalCount=d.TotalCount;
                    // if(d.Data.length>0){
                      t.tableData=d.Data;  
                    // }
                    
                },complete:function(){
                    t.loading=false;
                }
            })
                //此处执行搜索ajax请求
            },
            show (index) {
                this.$Modal.confirm({
                    render: (h) => {
                        return h("div",[
                            h(changeUser,{
                                props: {
                                    "userInfo":this.tableData[index],
                                    "groups":[{value:0,label:"管理员"},{value:1,label:"普通用户"}]
                                     }
                                })                            
                        ])
                    }
                })
            },
            remove (index) {
                this.tableData.splice(index, 1);
            }
        },created(){
            var t=this;
            t.getTopicList();
            console.log(t.base.formateDate(1530326724))
            
        }
    }
</script>



