<template>
    <div id="register" class="full-screen ub ub-ver">
        <div class="ub-f1"></div>
        <div style="width:320px;margin:0 auto;">
            <div class="text-center mb-20">
                <img src="/static/images/logo.png" style="width:80px;height:80px;border-radius:50%">
                <p class="mt-20 text-14">欢迎来到牛票票管理后台，您需要注册成为管理员吗？</p>
            </div>
            <form class="p-15" id="loginForm">
                <p class="mb-20">
                    <Input v-model="registerData.phone" placeholder="请输入您的电话号码"></Input>
                </p>
                <p class="mb-20">
                    <Input v-model="registerData.name" placeholder="请输入您的昵称"></Input>
                </p>
                <p class="mb-20">
                    <Input v-model="registerData.password" placeholder="请输入您的登录密码"></Input>
                </p>
                <Button type="primary" size="large" long @click="register()">注 册</Button>
            </form>
            <p class="text-14 p-15">已有账号<a href="/login" class="text-blue">立即登录</a></p>
        </div>
        <div class="ub-f1"></div>
    </div>
</template>

<script>
export default {
    name:"register",
    data:function(){
        return {
            registerData:{}
        }
    },methods:{
        register:function(){
            var t=this;
            $.ajax({
                url:t.base.baseUrl+"/register",
                type:"post",
                data:t.registerData,
                success:function(d){
                   if(d.Code==0){
                       t.$router.push({path: '/login'});
                   }
                }
            })
        }
    }
}
</script>

<style>

</style>




