<template>
    <div id="topicDetail">
        <div class="ub">
            <Card style="width:375px;height:667px;overflow-x:hidden;overflow-y:auto;margin-right:30px;">
                <topicDetailPage :article="article" />
            </Card>
            <div class="ub-f1">
                <Tabs type="card">
                    <TabPane label="编辑内容">
                        <Form :label-width="80">
                            <FormItem label="标题">
                                <Input v-model="article.Title"></Input>
                            </FormItem>
                            <FormItem label="内容">
                                <Input v-model="article.Content" type="textarea" :autosize="{minRows: 5,maxRows:10}" placeholder="Enter something..."></Input>
                            </FormItem>
                            <FormItem>
                                <Button @click="editArticle()" type="primary">保存</Button>
                            </FormItem>
                        </Form>
                    </TabPane>
                    <TabPane label="状态管理">
                        <Form :label-width="80">
                            <FormItem label="推荐语">
                                <Input v-model="article.RecommendText"></Input>
                            </FormItem>
                            <FormItem label="内容">
                                <Select v-model="article.State">
                                    <Option v-for="(value,key) in articleState" :key="key" :value="key" >{{value}}</Option>
                                </Select>
                            </FormItem>
                            <FormItem>
                                <Button @click="changeArticleState()" type="primary">保存</Button>
                            </FormItem>
                        </Form>
                    </TabPane>
                    <TabPane label="中奖处理">标签三的内容</TabPane>
                </Tabs>
            </div>
        </div>
    </div>
</template>

<script>
import base from '@/base.js';
import topicDetailPage from "@/components/topic/topicDetailPage"
export default {
name:"topicDetail",
components:{topicDetailPage},
data:function(){
    return {
        articleState:{0:'普通',1:'推荐',2:'下沉',3:'被删除',4:'审核未过',},
        baseUrl:this.base.baseUrl,
        article:{}
    }
},
methods:{
    editArticle:function(){
        var t=this;
        $.ajax({
            url:base.baseUrl+"/EditArticle",
            type:"get",
            data:t.article,
            success:(d)=>{
                console.log(d)
            }
        })
    },changeArticleState:function(){
        var t=this;
        $.ajax({
            url:base.baseUrl+"/ChangeArticleState",
            type:"get",
            data:t.article,
            success:(d)=>{
                console.log(d)
            }
        })
    }
},
created:function(){
    var t=this;
    var topicid=t.$route.params.topicId;
    $.ajax({
        url:t.base.baseUrl+"/GetSingleArticle",
        type:"get",
        data:{aid:topicid},
        success:function(d){
            t.article=d.Data
        }
    })
}
}
</script>
<style>

</style>



