<template>
    <div id="login" class="full-screen ub ub-ver">
        <div class="ub-f1"></div>
        <div style="width:320px;margin:0 auto;">
            <div class="text-center mb-20">
                <img src="/static/images/logo.png" style="width:80px;height:80px;border-radius:50%">
                <p class="mt-20 text-14">欢迎来到牛票票管理后台，请管理员先登录！</p>
            </div>
            <form class="p-15" id="loginForm">
                <p class="mb-20">
                    <Input v-model="loginData.phone" placeholder="请输入您的电话号码"></Input>
                </p>
                <p class="mb-20">
                    <Input v-model="loginData.password" placeholder="请输入您的登录密码"></Input>
                </p>
                <Button type="primary" size="large" long @click="login()">登 录</Button>
            </form>
            <p class="text-14 p-15">没有账号<router-link to="register" class="text-blue">立即注册</router-link><router-link to="findPassword" class="text-blue float-r">忘记密码？</router-link></p>
        </div>
        <div class="ub-f1"></div>
    </div>
</template>

<script>
export default {
    name:"login",
    data:function(){
        return {
            loginData:{}
        }
    },methods:{
        login:function(){
            var t=this;
            $.ajax({
                url:t.base.baseUrl+"/login",
                type:"post",
                data:t.loginData,
                success:function(d){
                   if(d.Code==0){
                       
                       t.$router.push({path: '/'});
                   }
                }
            })
        }
    }
}
</script>

<style>

</style>




